<template>
    <form @submit.prevent="updateEgr" >
              <div>
  
                  <input
                      type='text'
                      name='descripcion'
                      class='form-control mt-3'
                      placeholder='Descripcion'
                      autoComplete='off'
                      v-model="editIngreso.descripcion"
  
                  />
                   <input
                      type='number'
                      name='monto'
                      class='form-control mt-3'
                      placeholder='Monto'
                      autoComplete='off'
                      v-model="editIngreso.monto"
  
                  />
                  
  
                  <button 
                  class='btn btn-primary form-control  mt-2'
                  type='submit'  
                  
                  >
                  Actualizar
  
                  </button>
  
              </div>
          </form>
          <button class="btn btn-success mt-5" @click="salirEdit" >Salir</button>
  </template>
  
  <script>
  import { mapActions, mapMutations, mapState } from 'vuex'
  export default {
      data(){
          return{
              editIngreso:{}
          }
      },
      mounted(){
          this.editIngreso = this.ingreso
      },
      computed:{
          ...mapState('ferneyIngreso',['ingreso']),
      },
      methods:{
       ...mapActions('ferneyIngreso',['updateIngreso']),
       ...mapMutations('ferneyIngreso',['salirEdit']),
  
       async updateEgr() {
              
              await this.updateIngreso(this.editIngreso)
         }
      }
  }
  </script>
  
  <style>
  
  </style>